/* eslint-disable import/prefer-default-export */
import { allFilter } from './constants';

export const getTabFilters = searchFilters => {
    let tabFilters = [];

    if (Array.isArray(searchFilters) && searchFilters.length > 0) {
        tabFilters = searchFilters.map(filter => ({
            id: filter.sysId,
            label: filter.label,
        }));
    }

    return [allFilter, ...tabFilters];
};
