import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tabs';

import view from './view';
import { NOW_TABS_ITEM_SELECTED } from './actions';
import { nowTabsSelectedEffect } from './effects';
import { allFilterId } from './constants';

/**
 * Seismic component for tab filters - that can be used to filter search results based on defined sources.
 * The component adds an All tab as the first tab and renders the other tabs in given order.
 *
 * ```jsx
 * <sn-tab-filter searchFilters={[
    {
		sysId: "947110427310101052c7d5fdbdf6a73e",
		label: "Knowledge Article"
	},
	{
		sysId: "fca0ed907320101052c7d5fdbdf6a799",
		label: "Catalog Items"
	},
]} />
 * ```
 *
 * @seismicElement sn-tab-filter
 * @summary Tab filters for navigating between search results.
 * @uib.label Tab filter
 * @uib.icon tile-uib-sn-tab-filter
 * @uib.description Seismic Component for Tab Filters
 * @uib.category primitives
 * @uib.associatedTypes global.core, global.layout
 * @uib.properties searchFilters, selectedFilters, hideLabel, fixedWidth, maxWidth
 * @uib.actions SN_TAB_FILTER#FILTER_SELECTED
 */
createCustomElement('sn-tab-filter', {
    renderer: { type: snabbdom },
    view,
    properties: {
        /**
         * The searchFilters response from the Search Filter GraphQL API, it contains the data for tab items with sysId and label.
         * @type {array}
         * @uib.description Specifies the items for tab filters
         * @uib.defaultValue []
         */
        searchFilters: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        /**
         * Specifies the ID of the selected filter tab. If this property is not passed in, All filter tab is selected by default.
         * @type {array}
         * @uib.description Specifies the selected filter tab
         * @uib.defaultValue []
         */
        selectedFilters: {
            default: [allFilterId],
            schema: {
                type: 'array',
            },
        },
    },
    actionHandlers: {
        [NOW_TABS_ITEM_SELECTED]: {
            effect: nowTabsSelectedEffect,
            stopPropagation: true,
        },
    },
    dispatches: {
        /**
         * Action dispatched when a tab filter is selected.
         * The payload key `selectedFilter` contains the JSON stringified array containing the `sys_id` of the tab filter selected by the user.
         * @type {{selectedFilter: (string)}}
         * @uib.description Dispatched when user selects any of the tab filters
         */
        TAB_FILTER_ITEM_SELECTED: {
            schema: {
                type: 'object',
                properties: {
                    selectedFilter: { type: 'string' },
                },
            },
        },
    },
});
