/* eslint-disable import/prefer-default-export */
import { TAB_FILTER_ITEM_SELECTED } from './actions';
import { allFilterId } from './constants';

export const nowTabsSelectedEffect = ({ action, dispatch }) => {
    const {
        payload: { value },
    } = action;

    // Search GQL API expects JSON stringified array of filter ids.
    const filters = JSON.stringify(value === allFilterId ? [] : [value]);

    dispatch(TAB_FILTER_ITEM_SELECTED, {
        selectedFilter: filters,
    });
};
