import { getTabFilters } from './utils';
import { allFilterId } from './constants';

const _getSelectedFilter = filters => {
    if (filters.length > 0) return filters[0];

    return allFilterId;
};

const view = ({ properties }) => {
    const { searchFilters, selectedFilters } = properties;
    const selectedFilter = _getSelectedFilter(selectedFilters);
    const filters = getTabFilters(searchFilters);

    return <now-tabs items={filters} selectedItem={selectedFilter} />;
};

export default view;
